@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@800;900&display=swap);
* {
  font-family: Helvetica, Arial, sans-serif;
  padding: 0;
  margin: 0;
}

body{
  padding: 60px;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
input{font-size:1.2rem;padding:10px;color:grey;border-radius:5px;outline:none;min-width:200px;margin:20px 0px;width:100%;box-sizing:border-box}

.card{display:table;align-content:center;border:none;border-radius:6px;overflow:hidden;background-color:#DDE5D8;padding:8px;margin:5px;height:auto;max-height:auto;min-height:auto;box-shadow:2px 5px 5px #4D5242}.card p{padding:5px;text-align:center}.card img{max-height:auto;max-width:auto;width:100%;height:auto;object-fit:fill}

.card-grid-title{font-size:32px;text-align:center;margin:10px 0px;text-transform:capitalize;color:#282d2e;font-weight:bold}.card-grid{border:2px dashed #4D5242;box-sizing:border-box;border-radius:5px;margin:15px 0px;padding:15px;min-width:200px}

.main-title{font-size:36px;text-shadow:2px 5px 5px #4D5242}

@media only screen and (max-width: 500px){body{padding:5px;background-color:red}.card-grid{padding:5px}}

*{font-family:'Inconsolata',sans-serif}body{background-color:#B9E1CC}body::-webkit-scrollbar{width:10px}body::-webkit-scrollbar-thumb{border-radius:10px;-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.5)}body::-webkit-scrollbar-track{background-color:grey}body::-webkit-scrollbar-thumb{background-color:#201e1e}ol{width:100%;min-width:200px}

